import React, { useEffect, useCallback } from "react";
import { useTeInstance } from '../teConnectContext';
import { 
    framePath,
    frameDomain,
    PR_FRAME_ID,
    PR_FRAME_TITLE,
    PR_FRAME_NAME,
    PAYMENT_REQUEST_SESSION,
    TEC_APPLE_PAY_BTN_ID
} from '../consts';


const useTecPaymentsRequestInterface = instanciatedInterface => {
    console.log('inside hook');
    const { 
        frameRef, 
        formId, 
        prFrameUrl,
        requestClient: { instanceId }
    } = useTeInstance();

    const { 
        paymentsRequestInterface: { 
            createPaymentRequestToken: createPaymentBase, 
            canMakePayments 
        } 
    } = instanciatedInterface;

    const createPaymentRequestToken = _ => createPaymentBase({
        instanceId,
        formId,
        controllerRef: frameRef.current
    });


    console.log('leaving hook', createPaymentRequestToken);
    return {
        createPaymentRequestToken,
        canMakePayments,
        prFrameUrl
    }
}



const TecApplePayButton = ({ applePayPR, prInterface }) => {
    const {
        createPaymentRequestToken,
        canMakePayments,
        prFrameUrl
    } = useTecPaymentsRequestInterface(prInterface);

    const applePayClickHandler = e => {
        if (applePayPR) {
            const session = new ApplePaySession(3, applePayPR);
            console.log('session created', session);

            if (session) {
                console.log('setting handler');
                session.onvalidatemerchant = validateMerchantHandler;
                console.log('handler set');
            }


            console.log('calling "begin" method');
            session.begin();

            const listenForSessionResponse = e => {
                if (frameDomain === e.origin) {
                    if (e.data && typeof(e.data) === 'string') {
                        const { type, sessionNonce, sessionObj } = JSON.parse(e.data);
    
                        if (type === "_payment-requests-session-resp") {
                            if (sessionNonce === "4941c407-b54d-4beb-ac79-9dd70653b46e") {
                                console.log("received session OBJ on customer's side", sessionObj);
    
                                session.completeMerchantValidation(sessionObj);

                                session.onpaymentauthorized = e => {
                                    console.log('on payment authorized handler', e);
                                    console.log(e.payment);
                                    console.log(JSON.stringify(e.payment.token));
                                    console.log(e.payment.token.paymentData.data);
                                    session.completePayment({
                                        "status": ApplePaySession.STATUS_SUCCESS
                                    })
                                }
                            }
                            else    
                                console.log('session nonce doesnt match');
                        }
                        else
                            console.log('type was not response trigger')

                        window.removeEventListener('message', listenForSessionResponse, { passive: true });
                    }
                    else    
                        console.log('e.data was empty and/or not a string');
                }
                else
                    console.log('origin does not match', e.origin);
            }
    
            window.addEventListener('message', listenForSessionResponse, { passive: true });
        }
    }

    const tempHandler = async() => {
        console.log('testing createPaymentToken func');
        const test = await prInterface.createPaymentToken();

        console.log('[Client Test Result]:', test);
    }
    
    useEffect(() => {
        console.log('provided PR', applePayPR);
        console.log('prUrl: ', prFrameUrl);
        console.log('prInterface: ', paymentsRequestInterface);
    }, [prFrameUrl]);
    
    return (
        <>
            <style>{`
                .apple-pay-button {
                    display: inline-block;
                    -webkit-appearance: -apple-pay-button;
                    -apple-pay-button-type: plain;
                }
                .apple-pay-button-black {
                    -apple-pay-button-style: black;
                }
                .apple-pay-button-white {
                    -apple-pay-button-style: white;
                }
                .apple-pay-button-white-with-line {
                    -apple-pay-button-style: white-outline;
                }
            `}
            </style>

            <iframe
                id={ PR_FRAME_ID }
                title={ PR_FRAME_TITLE }
                width="100%"
                src={ prFrameUrl }
                loading="lazy"
                frameBorder="0"
                allowtransparency="true"
                scrolling="no"
                name={ PR_FRAME_NAME }
                ref={ frameRef }
                style={{ display: 'none' }}
            />

            <div 
                id={ TEC_APPLE_PAY_BTN_ID }
                class="apple-pay-button apple-pay-button-black"
                onClick={ createPaymentRequestToken
                    //tempHandler
                    //applePayClickHandler 
                }
                lang="en"
            ></div>
        </>
    );
    /*
        create an apple pay button component that accepts "white, black, whitewithline" and injects styles accordingly.
        Customers can expand upon that with their own style properties to spread.

        lang,
        button style: black, white, whitewithline
        button type: https://developer.apple.com/documentation/apple_pay_on_the_web/applepaybuttontype
        
    */
}

export default { 
    TecApplePayButton,
    useTecPaymentsRequest
};
